import React from 'react';
import { render } from '@testing-library/react-native';
import Dashboard from '../../src/screens/Dashboard';
import { ProgressProvider } from '../../src/contexts/ProgressContext';
import { ThemeProvider } from '../../src/contexts/ThemeContext';
import { CurrentLevelProvider } from '../../src/contexts/CurrentLevelContext';

jest.mock('expo-router', () => ({
  useFocusEffect: jest.fn(() => {}),
  router: { push: jest.fn(), back: jest.fn() },
}));

describe('Dashboard accessibility', () => {
  it('should expose accessible buttons for actions/levels', () => {
    const { getAllByA11yRole } = render(
      <ThemeProvider>
        <ProgressProvider>
          <CurrentLevelProvider>
            <Dashboard />
          </CurrentLevelProvider>
        </ProgressProvider>
      </ThemeProvider>
    );
    const buttons = getAllByA11yRole('button');
    expect(buttons.length).toBeGreaterThan(0);
  });
});


